
import React, { useEffect, useState, useRef } from "react";



import { getStore, updStore, gen16CodeId, parseDate, fetchPostH, getUrlServerSide, fetchEncryptStream, qryStore } from "../../../stores/Utils";

import { CheckBoxSlide_Cmpt, TextInput_Cmpt, CheckBox_Cmpt } from "../../../components";


import "./dashboard.css"
import { CloseModal } from "../../../components/DialogHRM";
import { PickProvider_Cmpt } from "../../ComprobanteParametrizados";
import { DateCalComponent_Cmpt } from "../../Inventory";

const CheckBoxSlide = CheckBoxSlide_Cmpt();
const TextInput = TextInput_Cmpt();

const CheckBox = CheckBox_Cmpt();


const PickProvide = PickProvider_Cmpt();

const DateCalComponent =DateCalComponent_Cmpt();





const frmNm = "new_pay_check_future"

const AddPayChecksOnFuture  = ({ data, confirm }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			
			set_obs(gen16CodeId())
		}
	});

	const updFrm = (f, v, k) => {
		let frm2 = getStore(frmNm) || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore(frmNm, frm2);
		
		set_obs(gen16CodeId())
	};


	const save  = async ()  => {
		let ffm = getStore(frmNm);

		ffm["payFutureCheckId"] = gen16CodeId();
		let bdyq2 = {...getStore(qryStore())?.add_future_check};
		bdyq2.params = {
			businessId:  getStore("profileUser")?.businessId,
		};
		bdyq2.form = ffm;
		const res = fetchEncryptStream(bdyq2);
		const td = await res;
		if (td) {
			updStore(frmNm, {});
			confirm && confirm();
			data?.modalID && CloseModal({id:data?.modalID})
		}
		
	};



	const clean = (f) => {
		updStore(frmNm, {});
		set_obs(gen16CodeId())
	};

	
	
	const  handlePick = (v) =>{
		updFrm("providerId",v.providerId);
		set_obs(gen16CodeId())
	}

	const  clear = () =>{
		updFrm("providerId",null);
		set_obs(gen16CodeId())
	}

	let accountD = getStore("providers_ids")?.[getStore(frmNm)?.["providerId"]];
	

	return (
		<div style={{
			maxHeight: "80vh",
			width: "650px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px 30px"
		}}>
			<div className="_dsplFlx ">
				<p className="form_title">
					Agregar Cheque a Futuro
				</p>
				<div className="flexSpace"/>
				<div className="_dsplFlx "  style={{marginTop:3}}>
					<div className="flexSpace"/>
					<DateCalComponent label={"Fecha a pagar"} init={getStore(frmNm)?.["date"]} updDate={(e)=>updFrm("date",e)} />
				
				</div>
			</div>



			<div style={{marginTop:30	}}>
				{accountD?.name ? 
				<div className="_dsplFlx " style={{margin: "18px 0"}}>
					<div className=" acc_dtls">
						<div className="_dsplFlx"  >
							<div className="code" style={{color: "#646cff"}} onClick={clear}>
								{accountD?.name} 
							</div>
						</div>
						<div className="flexSpace"/>
						
					</div>
					
				</div> :
				<PickProvide
					placeholder={"buscar una provedor"} 
					width={320}
					handleClick={handlePick}
				/>
				}
			</div>
			
			<div className="_dsplFlx "  style={{marginTop:30}}>
				<div className="flexSpace"/>
				<TextInput  
					init={getStore(frmNm)?.["checkOrSlip"]} 
					label={"No Cheque"}
					width={120} 
					updChanges={(e)=>updFrm("checkOrSlip",e)}
				/>
				<div className="flexSpace"/>
				<TextInput  
					init={getStore(frmNm)?.["document"]} 
					label={"Documento"} 
					width={120} 
					updChanges={(e)=>updFrm("document",e)}
				/>
				

				<div className="flexSpace"/>
				<TextInput  
					init={getStore(frmNm)?.["amount"]} 
					label={"Importe"} 
					width={120} 
					updChanges={(e)=>updFrm("amount",e)}
				/>
				<div className="flexSpace"/>
			</div>
			

		

			
			<div className="_dsplFlx " style={{margin: "10px 0"}}>
				<div className="flexSpace"/>
				{getStore(frmNm)?.["document"] && getStore(frmNm)?.["amount"] && getStore(frmNm)?.["checkOrSlip"] && getStore(frmNm)?.["date"]  && getStore(frmNm)?.providerId?
				<div className="">
					<button onClick={save}>
						Guardar
					</button>
				</div>
				:null}

			</div>
		

			
		</div>
	);
};

export default AddPayChecksOnFuture;



